              departmentId: `${Math.floor(Math.random() * 1000000000000)}`,
              parentId: 0,
              level: item && item.level || 1,
              name: `1部`,
              children: data
            }
            if (typeof(node) === 'string') {
              return [parent]
            }
            return data
          }
          setTimeout(() => {
            const data = getData(length)
            console.log(data)
            resolve(data)
